import React from 'react';
import PropTypes from 'prop-types';
import { format } from 'date-fns';
import ContainerDimensions from 'react-container-dimensions';
import _ from 'lodash';

import {
  VictoryChart,
  VictoryTheme,
  VictoryAxis,
  VictoryLine,
  VictoryScatter,
  VictoryTooltip,
  VictoryLegend,
  VictoryVoronoiContainer,
} from 'victory';

import { formatDollars } from '../../lib/string-utils';
import styles from './chart.module.css';

const theme = {
  ...VictoryTheme.material,
  chart: {
    scale: { x: 'time' },
    padding: { top: 64, right: 64, bottom: 360, left: 80 },
    domainPadding: { y: 20 },
  },
};

const GenericChart = ({ title, primaryData, primaryColor }) => (
  <div>
    <div className={styles.title}>{title}</div>
    <ContainerDimensions>
      {({ width }) => (
        <VictoryChart
          width={width}
          height={640}
          theme={theme}
          containerComponent={
            <VictoryVoronoiContainer
              voronoiDimension="x"
              voronoiBlacklist={['primaryLine']}
              labels={({ y, l }) => `${l}: ${formatDollars(y)}`}
              labelComponent={<VictoryTooltip cornerRadius={4} flyoutStyle={{ fill: 'white' }} />}
            />
          }
        >
          <VictoryLine
            name="primaryLine"
            data={primaryData}
            style={{ data: { stroke: primaryColor } }}
          />
          <VictoryScatter
            data={primaryData}
            size={(d, active) => (active ? 6 : 4)}
            style={{ data: { fill: primaryColor } }}
          />

          <VictoryAxis dependentAxis tickFormat={y => formatDollars(y)} />
          <VictoryAxis
            style={{ tickLabels: { angle: 32, padding: 16, textAnchor: 'start' } }}
            tickFormat={x => format(new Date(x), 'MMM D')}
          />

          <VictoryLegend
            x={width / 2 - 120}
            y={360}
            orientation="horizontal"
            gutter={12}
            borderPadding={{ top: 12, right: 16, bottom: 12, left: 16 }}
            style={{ border: { stroke: 'black' }, title: { fontSize: 20 } }}
            data={[
              { name: _.get(primaryData, '[0].l'), symbol: { fill: primaryColor } },
              { name: 'Two', symbol: { fill: 'orange' } },
              { name: 'Three', symbol: { fill: 'gold' } },
            ]}
          />
        </VictoryChart>
      )}
    </ContainerDimensions>
  </div>
);

GenericChart.propTypes = {
  title: PropTypes.string.isRequired,
  primaryData: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  primaryColor: PropTypes.string.isRequired,
};

export default GenericChart;
