import React, { Component } from 'react';
import { format, subYears } from 'date-fns';
import _ from 'lodash';

import { FacebookLoader } from './components/loading-spinner';
import { GenericChart } from './components/chart';
import styles from './app.module.css';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      historicalBtcPrices: [],
      isLoadingHistoricalBtcPrices: true,
    };
  }

  componentDidMount() {
    const today = format(new Date(), 'YYYY-MM-DD');
    const past = format(subYears(new Date(), 2), 'YYYY-MM-DD');
    const url = `https://api.coindesk.com/v1/bpi/historical/close.json?start=${past}&end=${today}`;
    fetch(url)
      .then(res => res.json())
      .then(result => {
        this.setState({
          isLoadingHistoricalBtcPrices: false,
          historicalBtcPrices: result.bpi,
        });
      });
  }

  renderCharts() {
    const { historicalBtcPrices } = this.state;
    const totalHistorySize = _.size(historicalBtcPrices);

    return (
      <div className={styles.chartContainer}>
        <GenericChart
          title="BitcoinMarkets daily comments vs. BTC price"
          primaryColor="blue"
          primaryData={_.map(historicalBtcPrices, (price, date) => ({
            x: date,
            y: price,
            l: 'BTC Price',
          })).slice(totalHistorySize - 30, totalHistorySize)}
        />
      </div>
    );
  }

  render() {
    const { isLoadingHistoricalBtcPrices } = this.state;

    return (
      <div className={styles.container}>
        {isLoadingHistoricalBtcPrices ? <FacebookLoader /> : this.renderCharts()}
      </div>
    );
  }
}

export default App;
